import {Loading} from 'element-ui';

let func = {
    // 递归返回三级分类
    createClassify (arr) {
        // 判断数组是否为空
        if (Object.prototype.toString.call(arr) !== '[object Array]' || !arr.length) {
            return;
        }

        let result = [];

        arr.forEach(el => {
            result.push({
                value: el.category_id,
                label: el.c_name,
                pic: el.pc_pic,
                children: func.createClassify(el.child),
            });
        });
        return result;
    },

    //根据QueryString参数名称获取值
    getQueryStringByName (name) {

        let result = location.search.match(new RegExp('[\?\&]' + name + '=([^\&]+)', 'i'));

        if (result === null || result.length < 1) {
            return '';
        }

        return result[1];

    },

    // 视频检测并提取src
    checkVideo (iframe) {

        let video = iframe && iframe.trim() || '';
        let result;

        if (video !== '' && !video.match(/<iframe.+src.+<\/iframe>/)) {
            result = 0;
        } else {
            let matched = video.match(/src=['|"]?(http[^'"\s]+)(['|"]?\s)/);
            result = matched && matched[1] || '';
        }

        return result;
    },

    // 加载动画 1s
    loading () {
        let lvm = Loading.service();
        setTimeout(() => {
            lvm.close();
        }, 1000);
    },

    // 获取元素绝对位置
    offset(ele) {
        let top = ele.offsetTop;
        let left = ele.offsetLeft;
        while (ele.offsetParent) {
            ele = ele.offsetParent;
            top += ele.offsetTop;
            left += ele.offsetLeft;
        }
        return {
            left: left,
            top: top,
        };
    },
};

export default func;


