// import Vue from 'vue';
import Vuex from 'vuex';

// Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        area: null,
        user_id: null,
        edit_annou: null,
    },
    mutations: {
        area (state, payload) {
            state.area = payload;
        },

        user_id (state, payload) {
            state.user_id = payload;
        },

        // 编辑公告 中转
        edit_annou (state, payload) {
            state.edit_annou = payload;
        },

    },
});

export default store;


