<template>
    <div>
        <div class="banner list">
            <div class="banner-wrap">
                <h2 class="banner-tit">优质企业</h2>
            </div>
        </div>

        <div class="comp-list-contain">
            <div class="wrap">

                <div class="comp-list-left quality-comp">
                    <ul v-if='totalItems'>
                        <li class="search-item" v-for="item in listData">
                            <div class="search-item-left fl">
                                <div class="search-img-contain">
                                    <img :src="item.company_url" alt="企业">
                                </div>
                            </div>
                            <div class="search-item-middle fl">
                                <h3>
                                    <span class="search-item-tit"> {{item.company_name}}</span>
                                    <span class="icon-authen junior" v-if="item.is_authentication == 1">认证企业</span>
                                    <span class="icon-authen senior" v-if="item.is_authentication == 2">高级认证</span>
                                </h3>

                                <div class="search-item-text">
                                    <p class="search-item-text-tit">业务范围：</p>
                                    <p class="search-item-busni-text">{{item.business}}</p>
                                    <p class="search-item-adress">
                                        <span class="search-item-text-tit">地址：</span>
                                        <span class="search-item-adress-text">{{item.company_address}}</span>
                                    </p>
                                </div>
                            </div>
                            <div class="search-item-right fr">
                                <i class="icon icon-qq" @click="qq(item.qq)"></i>
                                <a target="_blank" :href="`/pc/views/comp-detail.html?comp_id=${item.id}`">
                                    <button class="btn pink">查看详情</button>
                                </a>
                            </div>
                        </li>
                    </ul>
                    <!--分页-->
                    <my-page :total="totalItems" :message="message" @clickpage="turnPage"></my-page>
                </div>

            </div>
        </div>

    </div>
</template>

<script>
    import api from '../../public/api';
    import bus from '../../state/bus';
    import func from '../../public/func';
    let queryByName = func.getQueryStringByName;
    let createClassify = func.createClassify;

    export default {
        name: 'comp-list',
        data () {
            return {
                listData: [
                    // {
                    //     'id': 1,
                    //     'company_url': 'weqwe',
                    //     'company_video': 'asdasd',
                    //     'company_name': '上海网络科技',
                    //     'company_address': '上海,黄浦区',
                    //     'company_authentication': '1',
                    //     'tel': '4556456465',
                    //     'qq': '545654654',
                    //     'business': '1,2,3',
                    //     'introduce': '这是一家创新型公司',
                    //     'is_authentication': 1
                    // },
                ],

                annouData: [
                    // {
                    //     id: 0,
                    //     company_name: '',
                    //     notice_content: '',
                    //     notice_url: [],
                    //     notice_video: '',
                    //     create_time: '',
                    // },
                ],

                threeClassify: [],

                postData: {
                    area: '',
                    fid: '',
                    sid: '',
                    tid: '',
                    page: 1,
                },

                message: '暂无数据',
                totalItems: 1,

            };
        },
        computed: {
            user_id () {
                return this.$store.state.user_id || null;
            },

            area () {
                return this.$store.state.area || null;
            },

        },
        methods: {
            turnPage (page) {
                this.getListData(page);  // page为子组件传过来的页码，获取对应数据
            },

            // 获取列表
            getListData (page) {

                this.postData.page = page;
                this.$http.post(api.quality_company, {
                    area: this.$store.state.area,
                    more: 1,
                    page,
                })
                    .then(res => {
                        this.listData = res.data.company;
                        this.totalItems = res.data.count;

                        this.$loading().close();
                    })
                    .catch(err => {
                        console.log(err);
                    });
            },

            qq (qq) {
                if (!this.user_id) {
                    this.$message.warning('请登录后继续操作');
                } else {
                    window.open(`http://wpa.qq.com/msgrd?v=3&uin=${qq}&site=qq&menu=yes`)
                }
            },

        },

        beforeCreate () {
            this.$loading();
        },

        watch: {
            area () {
                if (this.area) {
                    this.getListData(1); // 获取列表数据
                }
            },
        },

    };
</script>


